/**
 * 调用该函数，返回一个配置对象
 * @param vaule 需要传递进来的dom元素
 * @returns 
 */
export default function (vaule) {
    return {
        methods: {
            handleScroll() {
                this.$bus.$emit("mainScroll", this.$refs[vaule]);
            },
            handleScrollTop(newScrollTop) {
                this.$refs[vaule].scrollTop = newScrollTop;
            },
        },
        mounted() {
            this.$bus.$on("mainScrollTop", this.handleScrollTop);
            this.$refs[vaule].addEventListener("scroll", this.handleScroll);
        },
        beforeDestroy() {
            this.$bus.$emit("mainScroll");
            //当切换组件时，假设ToTop组件中的show值 === true，
            //那么切换到其他组件时，由于没有重新触发setSelect事件，并且该事件已经被销毁了，所以
            this.$refs[vaule].removeEventListener(
                "scroll",
                this.handleScroll
            );
            this.$bus.$off("mainScrollTop", this.handleScrollTop);
        },
    }
}